
import { getYuce, getSchoolInfo, myInfo, getPaiming, getGoalCity, viewVolunteer, addVolunteer, delVolunteer } from '@/api/index'
import { errorMsg } from '@/utils/common'
import XProgress from 'vux/src/components/x-progress/index.vue'
import Box from 'vux/src/components/box/index.vue'

import BScroll from 'better-scroll'
import _ from 'underscore'
export default {
components: {
XProgress,
Box
},
data () {
return {
labelList: ['211', '双一流'], // 学校标签，如211， 双一流
batch: '',
category: '',
province: '',
score: '',
rank: 0,
curTab: 'tiao',

loading: false,
list: [],
wenList: [],
tiaoList: [],
baoList: [],
end: false,
schoolScroll: null,
loaded: false,

agreeDiag: '',
noview: false, // 蒙层
count: '',
websocket: [],
dialogPortVisible: true,
percentMsg: 0,
showPredict: true,
TimeId: -1,
goalCity: '', // 意向城市
showVolunteer: false,
volunteerList: [],
showEnough: false, // 志愿单已满
}
},
computed: {
openid () {
return this.$store.state.user.openid
}
},
mounted () {
this.getUserInfo()









if (this.$route.query.score !== undefined) {
this.score = this.$route.query.score
} else if (localStorage.score !== undefined) {
this.score = localStorage.score
}

if (this.score !== '') {
this.gotoBlur()
const a = document.querySelector('.predict_score')


a.parentNode.className = 'predict_second_input'
}
console.log('333333344444')


if (localStorage.agreeDiag !== undefined) {
this.agreeDiag = localStorage.agreeDiag
} else {
this.agreeDiag = 'show'
}
this.loaded = false

this.getGoalCity()


if (this.$route.query.pay !== undefined) {
if (this.$route.query.pay.toString() === '1') {
this.noview = false
} else if (this.$route.query.pay.toString() === '0') {

this.noview = true
errorMsg(this, '付款失败')

}
}
},








activated () {
console.log('actived')
this.getUserInfo()































},
methods: {
gotoRecord () {

this.$router.push({ path: '/record'})
},
getUserInfo () {
myInfo(this.openid).then(res => {
this.batch = res.data.data.batch || '暂无'
this.category = res.data.data.category || '暂无'
this.province = res.data.data.province || '暂无'
if (this.score !== '') {
this.gotoBlur()
}
})








},
getGoalCity () {

getGoalCity({
openid: this.openid
}).then(resp => {
if (resp.data.code === 0) {
this.goalCity = resp.data.data.city
this.getList()
}
})
},































handleInput () {
clearTimeout(this.TimeId)
this.TimeId = setTimeout(() => {
this.gotoBlur()
}, 1000)
console.log('分数：', this.score)
},
gotoBlur () { // 失去焦点获取排名
getPaiming({
openid: this.openid,
score: this.score
}).then(res => {
this.rank = res.data.data
console.log('失去焦点：', res.data)
})
},
toggleInput () {
this.$refs.scoreInput.focus()
const a = document.querySelector('.predict_score')

a.removeAttribute('placeholder')
a.parentNode.className = 'predict_second_input'
console.log(a)
},
rankInput () {
this.$refs.rankInput.focus()
},
gotoDetailPage (school) {
console.log(7894561313, school)
getSchoolInfo(school.schoolname, this.openid).then(res => {

this.loading = false
if (res.data.code === 505) {
this.show = true
} else if (res.data.code === 0) {
this.$router.push({ path: '/schoolInfo/' + this.openid + '/' + school.schoolname })
} else {
}
})
},
addVolunteer (schoolName) { // 添加志愿单
console.log('添加志愿', schoolName)
addVolunteer({
openid: this.openid,
schoolname: schoolName
}).then(res => {
if (res.data.code === 0) {
this.$vux.toast.show({
text: '添加到志愿单成功~',
width: '9em'
})
} else if (res.data.msg === '预约冲突' && res.data.code === 503) {
this.$vux.toast.show({
text: '已存在志愿单中~',
width: '9em'
})
} else if (res.data.msg === '志愿已满' && res.data.code === 508) {
this.showEnough = true




}
})
},
viewVolunteer () { // 查看志愿单
viewVolunteer({
openid: this.openid
}).then(res => {
if (res.data.code === 0) {
console.log('志愿单：', res.data.data)
this.volunteerList = res.data.data
this.showVolunteer = true
}
})
},
noViewVolunteer () {
this.showVolunteer = false
},
gotoDel (name) { // 移出志愿
console.log('移出：', name)
delVolunteer({
openid: this.openid,
schoolname: name
}).then(res => {
if (res.data.code === 0) {
this.viewVolunteer()
}
console.log(res.data)
})
},
gotoPage (name) {
this.$router.push({ name: name })
},
gotoMajorPlan (val) {
console.log('跳转到专业招生计划', val)
this.$router.push({
name: 'majorPlan',
query: {
schoolName: val
}
})
},
gotoComplete (name) {
this.$router.push({ path: '/complete?type=predict' })
},
changTab (i) {
this.curTab = i
this.list = this[i + 'List']
},
startSchoolPredict () { // 主学校预测
console.log('开始预测')
this.curTab = 'tiao'


this.percentMsg = 0
if (this.score === '' || isNaN(this.score)) {
errorMsg(this, '请输入正确分数')
return
}
this.getList()















},
startMajorPredict () { // 主专业预测
if (this.score === '' || isNaN(this.score)) {
errorMsg(this, '请输入正确分数')
return
}
this.getList()
},







getList () {
console.log('0000000000000')
if (this.score === '') {
return
}
this.loading = true
localStorage.setItem('score', this.score)
if (this.rank === '') {
this.gotoBlur()
var a = document.querySelector('.predict_paiming')
console.log('值：', a)

}


this.$nextTick(() => {
console.log('意向城市：', this.goalCity)
const _this = this
getYuce({
openid: this.openid,
score: this.score,
paiming: this.rank,
schoolcity: this.goalCity
}).then(res => {
this.loaded = true
this.loading = false
this.list = []
if (res.data.code !== 0) {
errorMsg(this, res.data.msg)
this.loaded = false
} else {
this.noview = !res.data.data.istrue
this.wenList = res.data.data.wentuo || []
this.tiaoList = res.data.data.chongci || []
this.baoList = res.data.data.baodi || []
this.list = this.tiaoList

if (this.list.length !== 0) {
this.end = true
} else {
this.end = false
}
}
this.init()

}).catch(function (err) {
console.log('异常：', err)

_this.loading = false
_this.loaded = true
console.log('异常2', _this.loading)
})
})
},
init () {
this.$nextTick(() => {
this.schoolScroll = new BScroll(this.$refs.schoolWrapper, {
click: true
})
})
},
closeAgree () {
this.agreeDiag = 'hide'
localStorage.setItem('agreeDiag', 'hide')
},
toPay () {
if (this.score === '') {
errorMsg(this, '请先输入分数')
return
}
location.href = 'http://zhongkeruitong.top/show/cee/pay/create?openid=' + this.openid + '&score=' + this.score

}
}
}



